body{
  background: rgb(43, 44, 48);
  display: flex;
  flex-direction: column;
  min-height: 100vh; 
  
  overflow-x: hidden;
  scroll-behavior: smooth;
  
} 
body::-webkit-scrollbar{
   background-color: rgb(18, 21, 31);
 }

 body::-webkit-scrollbar-thumb{
   background-color: rgb(159, 88, 0);
   border-radius: 10px;
 }

.app7{
  overflow: hidden;
  
}
.error{
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: xx-large;
  color: rgb(18, 21, 31);
}

.error a{
  text-decoration: none;
  text-transform: uppercase;
  color: rgb(159, 88, 0);
  transition: all 1s;
}

.error a:hover{
 font-size: 50px;

}

.App {
  text-align: center;
  
}

.header{
  width: auto;
  height: 50px;
  background: rgb(11, 14, 25);
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  
}

.footer{
  width: 100%;
  height: 10em;
  background: rgb(11, 14, 25) ;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: auto;
 
}
.footer img {
  height: 40%;
}

.body{
  min-height: 100vh;
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  
  
}

.container{
  display: grid;
  
  grid-template-columns: repeat(3, 1fr);
  padding: 10px;


}


@keyframes example {
  0% {
    top: 100vh; }
  100% {top: 0vh;}
}
.containerModal{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  bottom: -100vh;
  transition: ease-in-out 1s;

  overflow: auto;
  background: rgba(43,44,48,1);
  z-index: 1000;
  /*animation: example;
  animation-timing-function: linear;
  animation-fill-mode: both;
  animation-play-state: running;
  animation-duration: 0.5s ;*/

}

.containerModalAnimate{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0vh;
  position: fixed;
  overflow: auto;
  background: rgba(43,44,48,1);
  z-index: 1000;
  transition: ease-in-out 1s;
 
  /*animation: example;
  animation-timing-function: linear;
  animation-fill-mode: both;
  animation-direction: reverse;
  animation-duration: 1s ;
  animation-play-state: running;*/
}

.logoModal{
  height: 40%;
  width: 40%;
}
.containerLogo{
  width: 100%;
  height: 20em;
  margin: auto;
  padding: 40px 0;
}

.imgPelis{ 
  height: 600px;
  width: 100%;
  grid-column: 1 / 2;
  overflow: hidden;
  margin: auto;
  animation: imagenDetalle ease-in 2s;
  
  
}

@keyframes imagenDetalle {
  0%{opacity: 0;}
  100%{
    opacity: 1;
  }
}

.arrow{
  display: flex;
  justify-content: left;
  cursor: pointer;
}

.detail{
  /*background: rgb(11, 14, 25);*/
  /*border: solid 1px rgb(159, 88, 0);*/
  border-radius: 5px;
  height: 600px;
  width: 100%;
  grid-column: 2 / 8;
  margin: auto;
  color: white;  
  animation: Detalle ease-in 3s ;
}

@keyframes Detalle {
  0%{opacity: 0;
      color: darkorange;
  }
  100%{
    opacity: 1;
    color: white;
    transform: translateX(-500);
  }

}
.detail p {
  text-align: start;
  margin: 20px;

}
.detail ul li{
  text-align: start;
  font-weight: 100;
  margin: 20px;
}
.detail span{
  font-weight: bold;
  margin: 20px;
}
.reproductor{
  border: solid 1px rgb(159, 88, 0);
  border-radius: 5px;
  height: 400px;
  max-width: 80%;
  min-width: 80%;
  grid-column: 1 / 8;
  grid-row: 2;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  margin-top: 20px;
  
}

.contentBtn{
  display: grid;
  place-items: center;
  
}

.btn-ep{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 100%;
  flex-wrap: wrap;
  text-align: center;
}

.ep{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  text-align: center;
  
}


.btn{

  margin: auto;
  min-width: 80%;
  max-width: 80%;
  margin-top: 20px;
  
  
}
.btn-single{
  margin-left: 10px;
  border: 2px solid rgb(159, 88, 0);
  background: none;
  border-radius: 5px;
  padding: 5px 50px;
  color: white;
  cursor: pointer;
  transition: ease-in-out 0.2s;
  margin-bottom: 10px;
}

.btn-single:hover{
  transform: scale(1.05);
  background: rgb(159 88 0);
}

.btn-single-full{
  margin-left: 10px;
  border: 2px solid rgb(159, 88, 0);
  background: rgb(159, 88, 0);
  border-radius: 5px;
  padding: 5px 50px;
  color: white;
  cursor: pointer;
  transition: ease-in-out 0.2s;
  margin-bottom: 10px;
}

.video{
  margin: auto;
  margin-bottom: 50px;
  background: #000000;
  
  
}

.carruselDiv{
  width: 100%;
  display: grid;
  place-items: center;
}



.carrusel{
  display: flex;
  width: 600px;
  height: 600px;
  
}

.carrusel img{
  width: 0;
  flex-grow: 1;
  object-fit: cover;
  opacity: .8;
  transition: .3s ease-in-out;
  animation: carruselInit ease-in 2s ;
}

@keyframes carruselInit{
  0% {opacity: 0;}
  100% {opacity: 0.8;}
}



.carrusel img:hover{
  width: 200px;
  opacity: 1;
  filter: contrast(120%);
  
}




.card {
  /* Add shadows to create the "card" effect */
 /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);*/
  transition: 0.3s;
  min-width: 200px;
  min-height: 400px;
  max-height: 400px;
  max-width: 200px;
  margin: 10px;
  overflow: hidden;
  position: relative;
  animation: carruselInit ease-in 1.6s ;
  
}

.card:hover{
  transform: scale(1.2);
  z-index: 2;
}




.warnning{
  color: white;
  display: flex;
  flex-wrap: wrap ;
  justify-content: center;
  grid-column: 2;
  width: auto;
  
}
.warnning span{
  color: darkorange;
}

.card img {
  background-size: cover;
  width: 100%;
}



.yearPleis{
  margin: 0;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: space-between;
}

.yearCartel{
  color: white;
  background-color: black;
  margin-top: 0;
  padding: 4px;
  font-size: 12px;
  
}

.peliCartel{
  position: absolute;
  top: 0;
  right: 0;
  
  background-color: rgb(159, 88, 0);;
  color: white;
  margin-top: 0;
  padding: 4px;
  font-size: 12px;
 

}

.serieCartel{
  position: absolute;
  top: 0;
  right: 0;
  
  background-color: rgb(0, 101, 159);;
  color: white;
  margin-top: 0;
  padding: 4px;
  font-size: 12px;
 

}

.yearTime{
  display: flex;
  justify-content: space-between;
}

.cardDes{
  position: absolute;
  margin: auto;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.9);
  height: 80%;
  color: white;
  font-size: 12px;
 text-align: start;
 text-decoration: none;
 font-weight: 200;
 opacity: 0;
 transition: all ease-in-out 0.2s;
}
.cardDes p{
  margin-top: 25px;
  
}

.cardDes h2{
  font-size: 12px;
  text-align: center;
  
}

.cardDes:hover{
  opacity: 1;
}

img {
  border-radius: 5px;
  height: 80%;
  cursor: pointer;
  
}

.insta {
  position: fixed;
  right: 0;
  bottom: 40px;
  margin: 10px;
  width: 40px;
  height: 40px;

}

.texto{
  color: red;
  width: 100px;
  margin: auto;
}

.spinner{
  width: 10em;
  height: 10em;
  border-top: 1em solid darkorange ;
  border-right: 1em solid transparent;
  border-radius: 50%;
  margin: auto;
  animation: spinner 2s linear infinite;
}

.circle{
  width: 1em;
  height: 1em;
  background-color: darkorange;
  border-radius: 50%;
  margin-left: 8.5em;
  margin-top: 0.5em;
}

@keyframes spinner{
  100% {transform: rotate(360deg);}
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    
  }

  
 
  .detail{
  margin: 0;
  height: auto;
  
  }

  
  .detail p {
    
    margin: 5px;
  
  }
  .detail ul li{
   
    margin: 5px;
  }
  .detail span{
    
    margin: 5px;
  }
  .video{
    margin: auto;
    height: auto;
    width: auto;
  }
  .logoModal{
    height: 80%;
    width: 80%;
  }

  .carrusel{
    display: none;
  }

  
}